import * as ActionTypes from './actions/actionTypes';
import fetch from 'isomorphic-fetch';

const baseURL = typeof window === 'undefined' ? process.env.BASE_URL || (`http://localhost:${(process.env.PORT || 8000)}`) : '';

export const saveContactRequest = (data) => (dispatch) => fetch(`${baseURL}/api/saveContactRequest`, {
        method: 'post',
        body: JSON.stringify({contactRequest: data}),
        headers: new Headers({
            'Content-Type': 'application/json'
        })
    }).then((response) => dispatch({
        type: ActionTypes.CONTACT_REQUEST_SAVED
    }));

export const getCarers = () => (dispatch) => fetch(`${baseURL}/api/getCarers`)
    .then((response) => response.json())
    .then((response) => dispatch({
        type: ActionTypes.ADD_CARERS,
        carers: response.carers
    }));

export const getCarer = (id) => (dispatch) => fetch(`${baseURL}/api/getCarer?id=${id}`)
    .then((response) => response.json())
    .then((response) => dispatch({
        type: ActionTypes.SET_SELECTED_CARER,
        carer: response.carer
    }));

export const registerCarer = (data) => (dispatch) => fetch(`${baseURL}/api/saveCarer`, {
        method: 'post',
        body: JSON.stringify({carer: data}),
        headers: new Headers({
            'Content-Type': 'application/json'
        })
    }).then((response) => dispatch({
        type: ActionTypes.CARER_REGISTERED
    }));

export const avatarSelected = (avatar) => ({
    type: ActionTypes.AVATAR_SELECTED,
    avatar
});


